	module.exports = {"data":{"wordpressWpLessons":{"id":"1cfb0421-cbd7-5cd4-b5d0-42ce666395ce","wordpress_id":143,"title":"3D Printing Uses","slug":"3d-printing-uses","content":"<h2>Overview</h2>\n<p>There are numerous ways that 3D printing is being used in the real world. Every day there are stories popping up about new applications of 3D printing in different industries. These are some of the ways 3D printing is being used today.</p>\n<h2>3D Printing in Fashion</h2>\n<h3>Clothing</h3>\n<p>The fashion industry seems to have really taken to 3D printing and raised a lot of attention in March of 2013 when they showed off a 3D printed dress. The dress was designed by Michael Schmidt and Francis Bitonti. It was designed specifically for model Dita Von Teese and printed in Nylon by Shapeways.</p>\n<p><img src=\"http://dhf-website.s3.amazonaws.com/images/3D-printed-dress-600px7.jpg\" alt=\"3d printed dress\" /></p>\n<h3>Shoes</h3>\n<p>Another fashion item that you may not think of being 3D printed are shoes. Many designers are creating intricate shoes for fashion with 3D printers.</p>\n<p><img src=\"http://dhf-website.s3.amazonaws.com/images/3dPrinted-Shoes-600px6.jpg\" alt=\"3d printed shoes\" /></p>\n<p><img src=\"http://dhf-website.s3.amazonaws.com/images/3d-printed-shoes-2-600px3.jpg\" alt=\"3d printed shoes 2\" /></p>\n<h3>Jewelry</h3>\n<p>Jewelry is one of easiest to understand applications of 3D printing. Shapeways has an entire section of <a href=\"http://www.shapeways.com/jewelry?li=nav\" target=\"_blank\" rel=\"noopener\">custom designed jewelry</a> where designers can upload to and customers can buy from.</p>\n<p><img src=\"http://dhf-website.s3.amazonaws.com/images/3dprinted-ring-600px5.jpg\" alt=\"3d printed ring\" /></p>\n<p><a href=\"https://n-e-r-v-o-u-s.com/kinematics/\" target=\"_blank\" rel=\"noopener\">Kinematics</a> allows you to create custom jewelry that is then 3D printed for you. It allows you to create a one of a kind piece.</p>\n<p><img src=\"http://dhf-website.s3.amazonaws.com/images/3dprinted-necklace-600px4.jpg\" alt=\"3d printed necklace\" /></p>\n<h3>Wearable Tech</h3>\n<p>There is no doubt that tech and fashion are merging with the emergence of <b>wearable technologies</b>. With 3d printing and some ingenuity, you can create your own wearable technologies such as an LED Belt Buckle:</p>\n<p><img src=\"http://dhf-website.s3.amazonaws.com/images/3d-printed-belt-buckle-600px2.jpg\" alt=\"3d printed belt buckle\" /></p>\n<p>Or, how about a 3D printed activity band:</p>\n<p><img src=\"http://dhf-website.s3.amazonaws.com/images/3d-printed-activity-band-600px1.jpg\" alt=\"3d printed activity band\" /></p>\n<p>&nbsp;</p>\n<h1>3D Printing in Architecture</h1>\n<h3>3D Printing for Future Architects</h3>\n<p>Here is a concept video from LeFabShop of what the future of architects and electric cars could offer.</p>\n<p><iframe width=\"560\" height=\"315\" src=\"https://www.youtube.com/embed/Gh13cQAxQCA?rel=0\" frameborder=\"0\" allowfullscreen=\"allowfullscreen\"></iframe></p>\n<h3>Prototypes &amp; Models</h3>\n<p>3D printing is great for producing models or prototypes of buildings. In the photo below, Charles Overy, founder of LGM, shows a model of a resort in Vail, Colo. &#8220;We used to take two months to build $100,000 models,&#8221; he said, adding that now they cost about $2,000.</p>\n<p><img src=\"http://dhf-website.s3.amazonaws.com/images/3d-printed-architecture-600px1.jpg\" alt=\"3d printed architecture\" /></p>\n<h3>Your Dream House</h3>\n<p>Explore your imagination and build your dream home and then 3D print it!</p>\n<p><img src=\"http://dhf-website.s3.amazonaws.com/images/dhf-3d-architecture-600px5.jpg\" alt=\"dhf 3d architecture\" /></p>\n<h3>Blueprints</h3>\n<p>Take blueprints and convert them into actual models to get a better idea of a home&#8217;s layout:</p>\n<p><img src=\"http://dhf-website.s3.amazonaws.com/images/chateau-600px4.jpg\" alt=\"chateau\" /></p>\n<p><img src=\"http://dhf-website.s3.amazonaws.com/images/3dprinted-chateau-1-600px2.jpg\" alt=\"3d printed chateau 1\" /></p>\n<p><img src=\"http://dhf-website.s3.amazonaws.com/images/3dprinted-chateau-2-600px3.jpg\" alt=\"3d printed chateau 2\" /></p>\n<h2>Medical Uses of 3D Printing</h2>\n<h3>Prosthetics</h3>\n<p>This is an area of 3D printing that is really exciting. The &#8220;limited run&#8221; or &#8220;one-off&#8221; capabilities of 3D printing lend themselves very well to medical services because people are very unique. What fits one person, may not fit another person and 3D printing allows the medical field to create custom solutions for unique people.</p>\n<p><img src=\"http://dhf-website.s3.amazonaws.com/images/3d-printed-leg-600px1.jpg\" alt=\"3d printed leg\" /></p>\n<p>We are also seeing instances where <a href=\"http://3dprint.com/2438/50-prosthetic-3d-printed-hand/\" target=\"_blank\" rel=\"noopener\">open source 3D printed prosthetics are performing better than expensive prosthetics</a>.</p>\n<p><img src=\"http://dhf-website.s3.amazonaws.com/images/3d-printed-hand-600px2.jpg\" alt=\"3d printed hand\" /></p>\n<p>3D printing is offering new solutions for old problems. For example, a broken bone. If you have ever had a broken bone, you know how itchy and smelly a traditional cast can get. New 3D printed casts offer breathability and the ability to repel water that traditional casts could not do while still providing the rigidity needed for healing.<br />\nAdditionally, the ability to fully customize the casts allows the use of new techniques such as ultrasonic technologies that help the break heal faster. The two wires in the photo can be positioned over the actual break and send ultrasonic waves into the break which has been proven to heal bones faster.</p>\n<p>Read more: <a href=\"http://www.cnet.com/news/3d-printed-cast-uses-ultrasound-to-speed-healing/\" target=\"_blank\" rel=\"noopener\">3D-printed cast uses ultrasound to speed healing</a></p>\n<p><img src=\"http://dhf-website.s3.amazonaws.com/images/3d-printed-cast-600px1.jpg\" alt=\"3d printed cast\" /></p>\n<h3>Internal Organs</h3>\n<p>In this video, Anthony Atala talks about how 3D printing technology is being used to create human organs to address the high demand and low availability of suitable donors.</p>\n<p><iframe width=\"560\" height=\"315\" src=\"https://www.youtube.com/embed/bX3C201O4MA?rel=0\" frameborder=\"0\" allowfullscreen=\"allowfullscreen\"></iframe></p>\n<h3>Implants</h3>\n<p>3D printing is also being used to create replacement parts for humans that have failed us in some way. Everything as &#8220;simple&#8221; as a knee replacement to complex structures such as jaws and even skulls!</p>\n<p>Read more: <a href=\"http://singularityhub.com/2012/02/06/another-first-for-3d-printing-%E2%80%93-woman-receives-jaw-implant/\" target=\"_blank\" rel=\"noopener\">Another First for 3D Printing – Woman Receives Jaw Implant</a></p>\n<p><img src=\"http://dhf-website.s3.amazonaws.com/images/3d-printed-jaw-600px3.jpg\" alt=\"3d printed jaw\" /></p>\n<p>Recently, a woman received the first 3D printing skull implant. Due to a rare condition, her skull was thickening and putting pressure on her brain. They replaced her skull with a 3D printed one during a 23 hour operation.</p>\n<p>Read more: <a href=\"http://www.nbcnews.com/science/science-news/medical-first-3-d-printed-skull-successfully-implanted-woman-n65576\" target=\"_blank\" rel=\"noopener\">Medical First: 3-D Printed Skull Successfully Implanted in Woman</a></p>\n<p><img src=\"http://dhf-website.s3.amazonaws.com/images/3d-printed-skull-600px5.jpg\" alt=\"3d printed skull\" /></p>\n<h3>Accessibility</h3>\n<p>3D printing allows people who have unique needs to develop their own solutions for common problems they run into.</p>\n<p>&#8220;As a wheelchair-user I often need to overcome a step in front of a building, a shop, cafe or bar. Therefore I had the idea of printing me my own small ramp. It has to be as small as possible to carry it in my bag, when I&#8217;m on my way through Berlin. This is my first prototype. Please feel free to print and to develop it&#8230; Any feedback is appreciated!&#8221; &#8212;<a href=\"http://www.thingiverse.com/nanonan/about\" target=\"_blank\" rel=\"noopener\">Thingiverse User nanonan</a></p>\n<p><a href=\"http://www.thingiverse.com/thing:213181\" target=\"_blank\" rel=\"noopener\">http://www.thingiverse.com/thing:213181</a></p>\n<p><img src=\"http://dhf-website.s3.amazonaws.com/images/3d-printed-ramps-600px4.jpg\" alt=\"3d printed ramps\" /></p>\n<h2>Other Uses for 3D Printing</h2>\n<h3>3D Printed Tools</h3>\n<p>This 3d printed tool creates a Dremel powered from a vacuum and has the added bonus of sucking up it&#8217;s own dust!</p>\n<p><iframe width=\"560\" height=\"315\" src=\"https://www.youtube.com/embed/SuztgGhTRg0?rel=0\" frameborder=\"0\" allowfullscreen=\"allowfullscreen\"></iframe></p>\n<p><a href=\"http://www.thingiverse.com/thing:76369\" target=\"_blank\" rel=\"noopener\">http://www.thingiverse.com/thing:76369</a></p>\n<p><img src=\"http://dhf-website.s3.amazonaws.com/images/3dprinted-dremel-600px2.jpg\" alt=\"3d printed dremel\" /></p>\n<p>In December 2014, the first 3D Printer in Space was delivered to the International Space Station! Shortly after, the first tool was 3D printed in space. The wrench was designed by someone on Earth and emailed to the astronauts at the ISS where they easily downloaded the file and printed the tool.</p>\n<p><img src=\"http://dhf-website.s3.amazonaws.com/images/3d-printed-space-wrench-nasa-632.jpg\" alt=\"3d printed space wrench\" width=\"600\" /></p>\n"}},"pathContext":{}}

